"use client";

import { Check, Copy } from "lucide-react";
import { useState } from "react";
import type { ColorFormat } from "@/hooks/use-color-formats";

interface ColorFormatItemProps {
	format: ColorFormat;
}

export function ColorFormatItem({ format }: ColorFormatItemProps) {
	const [copied, setCopied] = useState(false);

	const handleCopy = async () => {
		try {
			await navigator.clipboard.writeText(format.value);
			setCopied(true);
			setTimeout(() => setCopied(false), 1500);
		} catch (err) {
			setCopied(false);
		}
	};

	return (
		<li className="flex items-center justify-between gap-4 rounded-xl border border-border/70 bg-card px-3 py-3 shadow-sm transition-all hover:-translate-y-[1px] hover:shadow-md">
			<div className="flex flex-col gap-1 min-w-0 flex-1">
				<span className="text-[11px] font-semibold text-muted-foreground uppercase tracking-[0.18em]">
					{format.name}
				</span>
				<code className="text-sm font-mono truncate text-foreground/90">
					{format.value}
				</code>
			</div>
			<button
				onClick={handleCopy}
				type="button"
				className="shrink-0 rounded-full border border-transparent bg-muted px-2.5 py-2 text-muted-foreground transition-colors hover:border-border hover:bg-background focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 focus-visible:ring-offset-background"
				aria-label={`Copy ${format.name} value`}
			>
				{copied ? (
					<Check className="h-4 w-4 text-emerald-500" />
				) : (
					<Copy className="h-4 w-4" />
				)}
			</button>
		</li>
	);
}
